@import url(~normalize.css/normalize.css);

.services .container article::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 50%, white 100%);
}

body {
  background: #FFF;
}

img {
  width: 100%;
}

.parallax {
  position: relative;
  background-attachment: fixed;
  background-position: center 0;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
}

h1 {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 45px;
  text-transform: uppercase;
  color: #FFF;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
}

h2 {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 40px;
}

h3 {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 35px;
  color: #FFF;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  text-transform: uppercase;
}

h4 {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 30px;
}

h5 {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 18px;
  text-transform: uppercase;
}

p {
  font-family: "Lora", serif;
  font-size: 18px;
}

/** Media alignment */

.alignnone {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  height: auto;
}

.aligncenter {
  display: block;
  margin: 1rem auto;
  height: auto;
}

.alignleft,
.alignright {
  margin-bottom: 1rem;
  height: auto;
}

@media (min-width: 30rem) {
  .alignleft {
    float: left;
    margin-right: 1rem;
  }

  .alignright {
    float: right;
    margin-left: 1rem;
  }
}

/** Captions */

/** Text meant only for screen readers */

.screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  color: #000;
  background: #FFF;
}

.btn {
  padding: 10px;
  background: #000;
  color: #FFF;
  text-decoration: none;
  text-transform: capitalize;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  display: block;
  width: 150px;
  text-align: center;
  margin: 1em auto 2em auto;
  border: 2px solid #000;
}

.btn:hover {
  background: #FFF;
  color: #000;
}

.banner {
  background: #1B2F3F;
  color: #FFF;
  font-size: 30px;
  font-weight: 400;
  padding: 20px;
  text-align: center;
}

.unity-link {
  display: inline-block;
  max-width: 120px;
  max-height: 23px;
  float: right;
}

.svg-unity-logo {
  width: 100%;
  height: auto;
  max-height: 23px;
  display: inline-block;
  vertical-align: middle;
}

.svg-unity-logo path {
  fill: #000;
}

.map-container {
  background: url("../images/map-bg.png");
  background-size: cover;
  background-position: center;
}

#map {
  margin: 0 auto;
  display: block;
}

#map path {
  fill: #1B2F3F;
}

#map path:hover {
  fill: #858585;
}

.tooltip {
  background: white;
  display: none;
  position: absolute;
  font-size: 10px;
}

.logo {
  width: 200px;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
}

.nav li {
  display: inline-block;
  margin-left: 30px;
  text-transform: uppercase;
  font-family: 'Muli', sans-serif;
  font-size: 18px;
}

.nav li a {
  color: #FFF;
  text-decoration: none;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
}

.nav li a:hover {
  color: #000;
  text-shadow: none;
}

.buynow a {
  color: #000 !important;
  background: #FFF;
  padding: 20px;
  text-shadow: none !important;
}

.buynow a:hover {
  color: #FFF !important;
  background: #000;
}

.nav-trigger {
  display: none;
  text-decoration: none;
  color: #FFF;
  font-size: 30px;
}

.closed::before {
  content: "\00D7";
  color: #000;
  position: absolute;
  font-size: 45px;
  left: 2px;
  bottom: -7px;
}

@media (max-width: 767px) {
  .nav-trigger {
    display: block;
    position: relative;
    z-index: 20;
  }

  .nav {
    display: none;
    position: absolute;
    box-sizing: border-box;
    margin: 0;
    padding: 100px 0;
    right: 0;
    top: 0;
    width: 50vw;
    height: 100vh;
    background: #FFF;
    z-index: 10;
  }

  .nav li {
    margin-left: 0;
    display: block;
  }

  .nav li a {
    color: #000;
    text-shadow: none;
    display: block;
    padding: 20px;
  }

  .buynow a {
    color: #FFF !important;
    background: #000;
  }
}

.header {
  height: 100vh;
  background-size: cover;
  text-align: center;
}

.custom-logo {
  width: 100px;
  height: auto;
}

.home .header {
  position: relative;
}

.home .header h1 {
  width: 50%;
  margin: 0 auto;
  position: relative;
  top: 35%;
  transform: translateY(-50%);
}

.home .header .banner {
  position: absolute;
  bottom: 0;
  width: 100%;
  box-sizing: border-box;
}

.home video {
  position: absolute;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -10;
}

.services {
  padding-bottom: 30px;
}

.services .container {
  display: flex;
  text-align: center;
}

.services .container article {
  flex-grow: 1;
  width: 33.3%;
  height: 600px;
  background-size: cover;
  position: relative;
  margin-bottom: 8em;
}

.services .container article div {
  position: absolute;
  bottom: -100px;
  width: 100%;
}

.services .container article div h4 {
  margin-top: 10px;
  margin-bottom: 0;
}

.services .container article div h5 {
  margin-bottom: 0;
}

.callout {
  height: 600px;
  background-size: cover;
  background-position: bottom;
  position: relative;
}

.callout .callout-text {
  position: absolute;
  width: 30%;
  bottom: 30px;
  left: 30px;
  z-index: 10;
}

.products {
  height: 600px;
  background-size: cover;
}

.products .container {
  width: 40%;
  float: right;
}

.products .container article {
  float: left;
  width: 45%;
  background: #FFF;
  padding: 20px 10px;
  box-sizing: border-box;
  text-align: center;
  margin-right: 5%;
}

.products .container article h2 {
  margin: 10px;
}

.latestposts {
  background: #1B2F3F;
  padding: 3em;
}

.latestposts .container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.latestposts .container article {
  flex-grow: 1;
  flex-basis: 0;
  align-self: stretch;
  display: flex;
  align-items: center;
  color: #FFF;
  padding: 2em;
}

.latestposts .container article:nth-of-type(-n+2) {
  border-right: 1px solid #979797;
}

.latestposts .container article .post-title {
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
}

.latestposts .container article img {
  width: auto;
  margin-right: 1em;
}

.latestposts .container article a {
  color: #FFF;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
}

@media (max-width: 767px) {
  .home .header h1 {
    width: auto;
  }

  .callout .callout-text {
    width: auto;
  }

  .services .container {
    display: block;
  }

  .services .container article {
    width: 100%;
    height: 400px;
  }

  .services .container article h3 {
    margin-top: 0;
    padding-top: 20px;
  }

  .products {
    height: 700px;
  }

  .products .container {
    width: auto;
  }
}




